import React, { useState, useMemo } from 'react';
import UseTimer from "../../hooks/useTimer";
import styled from "styled-components";

const Timer = () => {
    const [value, setValue] = useState(1);

    const { timer, startTimerHandler, stopTimerHandler, restartTimer } = UseTimer({initialState:value});

    const memoizedTimer = useMemo(() => timer, [timer]);

    console.log(memoizedTimer, 'time');

    return (
        <WrapperContainer>
            <Container>
            <h2>{memoizedTimer}</h2>
            <TypeInput
                size="small"
                type="number"
                value={value}
                onChange={(e) => setValue(parseInt(e.target.value))}
                min={1}
            />


               <div>
                   <Buttons onClick={startTimerHandler}>Start</Buttons>
                   <Buttons onClick={stopTimerHandler}>Stop</Buttons>
                   <Buttons onClick={restartTimer}>Restart</Buttons>
               </div>
            </Container>
        </WrapperContainer>
    );
};

export default Timer;

const WrapperContainer=styled.div`
display: flex;
  justify-content: center;
  margin-top: 50px;
`
const Container=styled.div`
  width: 350px;
  height: 250px;
  background-color: #61dafb;
  margin-top: 50px;
  border-radius: 20px;
  box-shadow: 10px -3px 40px 12px rgba(0,0,0,0.75);
  
`
const TypeInput=styled.input`
width: 120px;
  height: 25px;
  font-size:24px;
  font-weight: bold;
  border-radius: 8px;
  border: none;
  
`
const Buttons=styled.button`
width: 120px;
  height: 40px;
  background-color: darkgoldenrod;
  margin-right: 10px;
  margin-top: 15px;
  font-size: 20px;
  border: none;
  border-radius: 10px;
  color: white;
  font-weight: 500;
`