import React from 'react';
import UseCounter from "../../hooks/useCounter";
import styled from "styled-components";
import {Button} from "@mui/material"

const Counter = () => {
    const {count, incrementHandler, decrementHandler, resetHandler} = UseCounter(4, 2)
    return (
        <Container>
            <ContainerBlock>
                <h1>{count}</h1>

                <Button color="secondary" variant="contained" onClick={incrementHandler}>Increment</Button>
                <Button onClick={decrementHandler} variant="contained" color="success">
                    Decrement
                </Button>
                <Button onClick={resetHandler} variant="contained" color="error">
                    Restart
                </Button>
            </ContainerBlock>
        </Container>
    );
};

export default Counter;

const Container = styled.div`
  display: flex;
  justify-content: center;
`
const ContainerBlock = styled.div`
  width: 400px;
  height: 200px;
  background-color: #61dafb;
  margin-top: 50px;
  border-radius: 20px;
  box-shadow: 10px -3px 40px 12px rgba(0, 0, 0, 0.75);

`

